


.nav-item{
  font-family: 'Times New Roman', Times, serif;
  padding-left: 2px;
}
.nav-item:hover{
  text-decoration: underline;
}

.navbar{
  background-color: black;
  color: white
  ;
}

.description {
  font-size: 30px;
  background-color: rgb(0, 70, 52);
  border-radius: 2px; 
  padding: 1rem; 
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.726); 
  color: white;
  width: 98%;
  margin: 0 auto;
  text-align: center;
  
}

.description h1 {
  font-family: 'Times New Roman', Times, serif;
  color: #ffffff; 
background-color: rgb(252, 172, 0);
padding: 10px;
  margin-bottom: 20px;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.description p {
  color: #ffffff; 
text-decoration: underline;
  font-size: 20px;
  margin-bottom: 16px;
}

.section{
  display: flex;
  background-color: #3EECAC;
background-image: linear-gradient(19deg, #3EECAC 0%, #EE74E1 100%);

  padding: 5rem;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  border-radius: 2px;
  padding: 20px;
  box-shadow: 0 0 20px rgb(0, 0, 0);
  color: rgb(0, 0, 0);
  width: 98%;
  margin: 0 auto;
  padding: 1rem;
  text-align: center;
  
}

.section p{
 color: rgb(255, 255, 255);
  
}
.card img{
  padding: 1rem;
}
.card {
  
    
   
    transition: transform 0.3s ease-in-out;
  backdrop-filter: blur(10px);
  border: none;
  border-radius: 10px;
  overflow: hidden;
 
}
.card:hover {
  transform: scale(1.05);
}
#password-card {
  background: linear-gradient(135deg, rgba(239, 255, 12, 0.815), rgba(255, 157, 10, 0.856));
}

#bmi-card {
  background: linear-gradient(135deg, rgba(255, 10, 210, 0.8), rgba(243, 144, 185, 0.4));
}

#currency-card {
  background: linear-gradient(135deg, rgba(50, 241, 255, 0.8), rgba(255, 204, 153, 0.4));
}

#calculator-card {
  background: linear-gradient(135deg, rgba(54, 255, 54, 0.8), rgba(153, 255, 153, 0.4));
}

#temperature-card {
  background: linear-gradient(135deg, rgba(255, 0, 0, 0.8), rgba(209, 19, 19, 0.4));
}
#weather-card {
  background: linear-gradient(135deg, rgb(28, 183, 255), rgba(255, 102, 0, 0.918));
}
#randomimages-card {
  background: linear-gradient(135deg, rgba(189, 0, 0, 0.8), rgb(233, 90, 226));
}
.section h4{
  color: white;
font-family: 'Courier New', Courier, monospace;
}

@media (max-width: 768px) {
  .container {
    width: 90vw;
    height: auto;
  }

  .input-field {
    width: 80vw;
    font-size: 24px;
  }

  .range-container {
    width: 80vw;
  }

  .form-range {
    width: 50%;
  }

  .generate-button,
  .copy-button {
    width: 80vw;
    font-size: 18px;
  }
}

